import {IonAlert} from '@ionic/react';
import React, {useState} from 'react';
import * as FIcon from 'react-feather';
import {observer} from "mobx-react-lite";
import {useStores} from "../../models/root-store/root-store-context";
import './AdminCliente.css';
import {Page, SDataTable} from "../../components";
import {RouteComponentProps} from "react-router";
import {Rubro} from "../../services/api";
import {protectedRef, useApiToaster} from "../../helpers-ts";


export const AdminRubro: React.FC<RouteComponentProps> = observer(({history}) => {
  const rootStore = useStores()
  const [showAlert, setShowAlert] = useState(false);
  const [RubroEliminar, setRubroEliminar] = useState<Rubro | null>(null)
  const [RubroEditar, setRubroEditar] = useState<Rubro | null>(null)
  const [showModalEditar, setShowModalEditar] = useState(false);
  const [tituloModalEditar, setTituloModalEditar] = useState('');

  const DataTable = React.createRef<any>()
  const apiToaster = useApiToaster()

  const doEditar = (nombre: string) => {
    if (RubroEditar){
      RubroEditar.nombre = nombre
      const dtable = DataTable.current
      rootStore.environment.api.guardarRubro(RubroEditar).then((result: any) => {
        apiToaster(result, "Rubro guardado exitosamente")
        dtable.refresh()
      })
    }
  }

  const handleEditar = (row: any) => {
    setRubroEditar(row)
    setShowModalEditar(true)
    setTituloModalEditar(row.id === undefined ? "Crear rubro" : "Modificar rubro")
  }

  const handleEliminar = (row: any) => {
    setRubroEliminar(row)
    setShowAlert(true)
  };

  const eliminarRubro = () => {
    if (RubroEliminar) {
      const dtable = DataTable.current
      rootStore.environment.api.eliminarRubro(RubroEliminar.id).then((result: any) => {
        apiToaster(result, "Rubro eliminado exitosamente")
        dtable.refresh()
      })
    }
  }

  const renderAcciones = (row: any) => {
    return <div>
      <span className={'button ml-3'} onClick={() => {
        handleEditar(row)
      }}><FIcon.Edit size={14}/></span>
      <span className={'button ml-3'} onClick={() => {
        handleEliminar(row)
      }}><FIcon.Trash size={14}/></span>
    </div>
  }

  const list2paged_response = (lista: any[]) => {
    return {
      kind: 'ok',
      data: {
        count: lista.length,
        results: lista
      }
    }
  }

  const obtener_rubros = async (perPage: number, page: number, order: any, search: string) => {
    let respuesta = await rootStore.environment.api.rubros()
    return list2paged_response(respuesta.kind === 'ok' ? respuesta.rubros : [])
  }

  return (
    <Page login_required admin>
      <div className="page-wrapper" style={{display: "flex", height: "100vh", overflowY: "scroll"}}>
        <IonAlert
            isOpen={showModalEditar}
            onDidDismiss={() => setShowModalEditar(false)}
            cssClass='modal-menu-estilo'
            header={tituloModalEditar}
            inputs={[
              {
                name: 'Nombre',
                type: 'text',
                placeholder: 'Nombre de rubro',
                value: RubroEditar?.nombre,
              }
              ]}
            buttons={[
              {
                text: 'Cancelar',
                role: 'cancel',
                cssClass: 'btn-secondary',
              },
              {
                text: 'Guardar',
                cssClass: 'btn-primary',
                handler: (data) => {
                  doEditar(data.Nombre)
                }
              }
            ]}
        />
        <IonAlert
          isOpen={showAlert}
          onDidDismiss={() => setShowAlert(false)}
          header={'Eliminar rubro'}
          message={`Confirma que desea eliminar el rubro "${RubroEliminar?.nombre}"?`}
          buttons={[
            {
              text: 'Cancelar',
              role: 'cancel',
              cssClass: 'btn-secondary',
            },
            {
              text: 'Eliminar',
              cssClass: 'btn-primary',
              handler: () => {
                eliminarRubro();
              }
            }
          ]}
        />
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h3 className="text-themecolor">Rubros</h3>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item"><a onClick={() => history.push('/admin/')}>Inicio</a></li>
                <li className="breadcrumb-item active">Rubros</li>
              </ol>
            </div>
          </div>
          <ul className="nav nav-pills p-3 bg-white mb-3 align-items-center">
            <li className="nav-item ml-auto">
              <button className="btn btn-primary d-flex align-items-center px-3" id="add-cliente" onClick={() => {
                handleEditar({ nombre: null})
              }}>
                Crear Rubro
              </button>
            </li>
          </ul>
          <div className="card mb-0">
            <div className="card-body">
              <div className="table-responsive">
                <SDataTable
                  ref={protectedRef(DataTable)}
                  subHeader
                  obtenerDatos={obtener_rubros}
                  disablePagination={true}
                  columns={[
                    {
                      name: 'ID',
                      selector: 'id',
                      sortable: false,
                    },
                    {
                      name: 'Nombre',
                      selector: 'nombre',
                      sortable: false,
                    },
                    {
                      name: 'Acciones',
                      sortable: false,
                      cell: (row: any) => renderAcciones(row),
                    },
                  ]}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </Page>
  );
});

export default AdminRubro;
